.center {
  text-align: center;
}
.left {
  float: left;
  text-align: right;
  width: 50%;
  font-size: 20px;
  font-weight: bold;
  box-sizing: border-box;
  padding-right: 30px;
}
.right {
  float: right;
  text-align: left;
  width: 50%;
  font-size: 20px;
  font-weight: bold;
  box-sizing: border-box;
}
.clearfix:after {
  content: "";
  display: table;
  clear: both;
  margin-bottom: 10px;
}

/* period selector */
.period-selector select {
  height: 40px;
  width: 100px;
  border-radius: 10px;
  border-color: lightgray;
  background: white;
}

/* location selector */
.CheckSheet--hidden {
  height: 0;
  overflow: hidden;
}

.CheckSheet-container {
  padding-left: 5%;
  column-count: 4;
}

.CheckSheet label {
  display: block;
  text-align: left;
}

.CheckSheet button {
  height: 40px;
  width: 140px;
  border-radius: 5px;
  border-color: lightgray;
  background: white;
}
